export default class Thread
{
	constructor (wasm_wrapper)
	{
		this.wasm_wrapper = wasm_wrapper;
	}

	init ()
	{
		return true;
	}

	work (function_name, _data = [])
	{
		const promise =
			new Promise
			(
				resolve =>
				{
					const data = this.wasm_wrapper.exports[function_name](..._data);

					resolve({ data });
				},
			);

		return promise;
	}

	terminate () {}
}
