import Worker from './thread3.1.worker.js';



export default class Thread
{
	// TODO: add stack_size option.
	constructor (wasm_wrapper)
	{
		this.wasm_wrapper = wasm_wrapper;

		this.stack_addr = this.wasm_wrapper.exports.__malloc(this.wasm_wrapper.options.thread_stack_size);

		this.stack_pointer = this.stack_addr + this.wasm_wrapper.options.thread_stack_size;

		this.worker = new Worker();

		this.resolves = [ null ];
	}

	init ()
	{
		const promise =
			new Promise
			(
				resolve =>
				{
					this.worker.onmessage = resolve;

					const { code, memory, imports_thread: imports } = this.wasm_wrapper;

					const { stack_pointer } = this;

					this.worker.postMessage
					({
						code,
						memory,
						imports,
						stack_pointer,
					});
				},
			);

		return promise;
	}

	async work (function_name, data = [])
	{
		const promise =
			new Promise
			(
				resolve =>
				{
					let resolve_index = this.resolves.indexOf(null);

					if (resolve_index === -1)
					{
						resolve_index = this.resolves.length;
					}

					// LOG('resolve_index', resolve_index)

					this.resolves[resolve_index] = resolve;

					// this.worker.onmessage = resolve;

					// TODO: move to init() ?
					this.worker.onmessage = ({ data }) =>
					{
						// LOG('data.resolve_index', data.resolve_index)
						this.resolves[data.resolve_index]();

						this.resolves[data.resolve_index] = null;
					};

					this.worker.postMessage({ function_name, data, resolve_index });
				},
			);

		return promise;
	}

	// TODO.
	// work2 (function_list)
	// {
	// 	const promise =
	// 		new Promise
	// 		(
	// 			resolve =>
	// 			{
	// 				this.worker.onmessage = resolve;

	// 				this.worker.postMessage({ work_list });
	// 			},
	// 		);

	// 	return promise;
	// }

	terminate ()
	{
		this.worker.terminate();

		this.wasm_wrapper.exports.__free(this.stack_addr);

		// this.promise = null;
	}
}
